import { useEffect, useState } from 'react';
import { SetApi, SetSaveApi } from 'utils/Api';
import {
  Row,
  Col,
  Button,
  Form,
  Input,
  Card,
  InputNumber,
  TimePicker,
} from 'antd';
import moment from 'moment';

const SystemSet = () => {
  const [payForm] = Form.useForm();
  const [smsForm] = Form.useForm();
  const [withdrawForm] = Form.useForm();
  useEffect(() => {
    SetApi('pay').then((res) => {
      payForm.setFieldsValue({
        _id: res.data._id,
        ...res.data.sets,
      });
    });

    SetApi('sms').then((res) => {
      smsForm.setFieldsValue({
        _id: res.data._id,
        ...res.data.sets,
      });
    });

    SetApi('withdraw').then((res) => {
      withdrawForm.setFieldsValue({
        ...res.data.sets,
        _id: res.data._id,
        time: [moment(res.data.sets.time[0]), moment(res.data.sets.time[1])],
      });
    });
  }, []);

  const handlePaySet = (value) => {
    const {
      _id,
      payApiUrl,
      transferApiUrl,
      payId,
      payType,
      payKey,
      transferKey,
      jumpUrl,
      notifyUrl,
			backUrl
    } = value;
    SetSaveApi({
      _id: _id,
      type: 'pay',
      sets: {
        payApiUrl,
        payKey,
        transferApiUrl,
        transferKey,
        payId,
        payType,
        jumpUrl,
        notifyUrl,
				backUrl
      },
    });
  };

  const handleSmsSet = (value) => {
    const { _id, smsApiUrl, appKey, secretKey, title, content } = value;
    SetSaveApi({
      _id: _id,
      type: 'sms',
      sets: { smsApiUrl, appKey, secretKey, title, content },
    });
  };

  const handleWithdrawSet = (value) => {
    const { _id, count, time, fees, min, multi } = value;

    SetSaveApi({
      _id: _id,
      type: 'withdraw',
      sets: {
        count,
        time,
        fees,
        min,
        multi,
      },
    });
  };
  return (
    <>
      <Row gutter={16}>
        <Col className="gutter-row" span={12}>
          <Card title="支付通道设置" bordered={false} style={{ width: 600 }}>
            <Form
              name="control-hooks"
              onFinish={handlePaySet}
              labelCol={{ span: 6 }}
              form={payForm}
            >
              <Form.Item label="id" name="_id" style={{ display: 'none' }}>
                <Input disabled />
              </Form.Item>
              <Form.Item
                label="商户号"
                name="payId"
                rules={[{ required: true, message: '请输入商户号' }]}
              >
                <Input placeholder="请输入商户号" />
              </Form.Item>
              <Form.Item
                label="支付接口地址"
                name="payApiUrl"
                rules={[{ required: true, message: '请输入支付接口地址' }]}
              >
                <Input placeholder="请输入支付接口地址" />
              </Form.Item>
              <Form.Item
                label="支付密钥"
                name="payKey"
                rules={[{ required: true, message: '请输入支付密钥' }]}
              >
                <Input placeholder="请输入支付密钥" />
              </Form.Item>
              <Form.Item
                label="支付回调地址"
                name="notifyUrl"
                rules={[{ required: true, message: '请输入回调地址' }]}
                extra="http://你的域名/api/common/notify"
              >
                <Input placeholder="请输入回调地址" />
              </Form.Item>
              <Form.Item
                label="代付接口地址"
                name="transferApiUrl"
                rules={[{ required: true, message: '请输入代付接口地址' }]}
              >
                <Input placeholder="请输入代付接口地址" />
              </Form.Item>
              <Form.Item
                label="代付密钥"
                name="transferKey"
                rules={[{ required: true, message: '请输入代付密钥' }]}
              >
                <Input placeholder="请输入代付密钥" />
              </Form.Item>
              <Form.Item
                label="代付回调地址"
                name="backUrl"
                rules={[{ required: true, message: '请输入代付回调地址' }]}
                extra="http://你的域名/api/common/back"
              >
                <Input placeholder="请输入代付回调地址" />
              </Form.Item>
              <Form.Item
                label="支付通道编码"
                name="payType"
                rules={[{ required: true, message: '请输入支付通道' }]}
              >
                <InputNumber placeholder="请输入通道" />
              </Form.Item>
              <Form.Item
                label="跳转地址"
                name="jumpUrl"
                rules={[{ required: true, message: '请输入跳转地址' }]}
                extra="支付结束后用户跳转的地址，一般为域名即可"
              >
                <Input placeholder="请输入回调地址" />
              </Form.Item>
              <Form.Item wrapperCol={{ span: 4, offset: 10 }}>
                <Button type="primary" htmlType="submit">
                  保存
                </Button>
              </Form.Item>
            </Form>
          </Card>
        </Col>
        <Col className="gutter-row" span={12}>
          <Card title="短信接口设置" bordered={false} style={{ width: 600 }}>
            <Form
              name="control-hooks"
              onFinish={handleSmsSet}
              labelCol={{ span: 4 }}
              form={smsForm}
            >
              <Form.Item label="id" name="_id" style={{ display: 'none' }}>
                <Input disabled />
              </Form.Item>
              <Form.Item
                label="接口地址"
                name="smsApiUrl"
                rules={[{ required: true, message: '请输入接口地址' }]}
              >
                <Input placeholder="请输入接口地址" />
              </Form.Item>
              <Form.Item
                label="App Key"
                name="appKey"
                rules={[{ required: true, message: '请输入App Key' }]}
              >
                <Input placeholder="请输入App Key" />
              </Form.Item>
              <Form.Item
                label="Secret Key"
                name="secretKey"
                rules={[{ required: true, message: '请输入Secret Key' }]}
              >
                <Input placeholder="请输入Secret Key" />
              </Form.Item>
              <Form.Item
                label="企业签名"
                name="title"
                rules={[{ required: true, message: '请输入企业签名' }]}
                extra="用于向客户表示此验证码是由本公司发出"
              >
                <Input placeholder="请输入企业签名" />
              </Form.Item>
              <Form.Item
                label="短信内容"
                name="content"
                rules={[{ required: true, message: '请输入短信内容' }]}
              >
                <Input placeholder="请输入短信内容" />
              </Form.Item>
              <Form.Item wrapperCol={{ span: 4, offset: 10 }}>
                <Button type="primary" htmlType="submit">
                  保存
                </Button>
              </Form.Item>
            </Form>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col className="gutter-row" span={12}>
          <Card
            title="提现相关设置"
            bordered={false}
            style={{ width: 600, marginTop: '2rem' }}
          >
            <Form
              name="control-hooks"
              onFinish={handleWithdrawSet}
              labelCol={{ span: 6 }}
              form={withdrawForm}
            >
              <Form.Item label="id" name="_id" style={{ display: 'none' }}>
                <Input disabled />
              </Form.Item>
              <Form.Item
                label="每日最多提现次数"
                name="count"
                extra="设置每天提现次数"
                rules={[{ required: true, message: '请设置每天提现次数' }]}
              >
                <InputNumber min="0" />
              </Form.Item>
              <Form.Item
                label="提现开启时间"
                name="time"
                extra="只在设置的时间范围内开启提现功能"
                rules={[{ required: true, message: '请设置提现开启时间' }]}
              >
                <TimePicker.RangePicker format="HH:mm" />
              </Form.Item>
              <Form.Item
                label="提现手续费点位"
                name="fees"
                extra="即提现时要扣除的手续费百分比"
                rules={[
                  {
                    required: true,
                    message: '请设置即提现时要扣除的手续费百分比',
                  },
                ]}
              >
                <InputNumber min={0} />
              </Form.Item>
              <Form.Item
                label="最小提现额度"
                name="min"
                extra="一次性提现的最少额度"
                rules={[
                  { required: true, message: '请设置一次性提现的最少额度' },
                ]}
              >
                <InputNumber min={0} />
              </Form.Item>
              <Form.Item
                label="提现整数倍"
                name="multi"
                extra="设置提现时金额的倍数"
                rules={[{ required: true, message: '设置提现时金额的倍数' }]}
              >
                <InputNumber min={0} />
              </Form.Item>

              <Form.Item wrapperCol={{ span: 4, offset: 10 }}>
                <Button type="primary" htmlType="submit">
                  保存
                </Button>
              </Form.Item>
            </Form>
          </Card>
        </Col>
      </Row>
    </>
  );
};

export default SystemSet;
